<template>
  <!-- Form nhp -->
  <div class="modal">
    <div class="modal__container" @keydown="handleKeyModel">
      <div class="modal__content">
        <div class="modal__header">
          <div class="modal__header__left">
            <h1 class="header__title">Thng tin nhn vin</h1>
            <input class="" type="checkbox" id="" />
            <label for="">L khch hng</label>
            <input class="" type="checkbox" id="" />
            <label for="">L nh cung cp</label>
          </div>
          <div class="modal__header__rigth">
            <div
              class="item__icon modal__header__icon"
              v-tooltip.bottom="{
                content: 'Tr gip(F1)',
              }"
            >
              <div class="icon__18 icon__help"></div>
            </div>
            <div
              class="item__icon modal__header__icon margin__letf_8"
              @click="isCloseModal"
              v-tooltip.bottom="{
                content: 'ng (ESC)',
              }"
            >
              <div class="icon__18 icon__close"></div>
            </div>
          </div>
        </div>
        <div class="modal__body">
          <div class="modal__body__row">
            <div class="body__row__left">
              <div class="container__input input__160">
                <label class="input__label" for=""
                  >M <span class="required">*</span>
                </label>
                <input
                  class="input"
                  type="text"
                  placeholder="M nhn vin "
                  maxlength="25"
                  v-model="employee.employeeCode"
                  errorCode="errorCode"
                  ref="empCode"
                  :class="{ input__error: errors.errorCode }"
                  @blur="blurValidate"
                />
                <div :class="{ input__show__error: errors.errorCode }">
                  {{ errors.errorCode }}
                </div>
              </div>
              <div class="container__input input__margin_6 input__320">
                <label class="input__label" for=""
                  >H v tn <span class="required">*</span></label
                >
                <input
                  class="input"
                  type="text"
                  maxlength="100"
                  placeholder="Tn nhn vin"
                  ref="empName"
                  errorCode="errorName"
                  v-model="employee.employeeName"
                  :class="{ input__error: errors.errorName }"
                  @blur="blurValidate"
                />
                <div :class="{ input__show__error: errors.errorName }">
                  {{ errors.errorName }}
                </div>
              </div>
            </div>
            <div class="body__row__rigth">
              <div class="container__input input__160">
                <label class="input__label" for=""> Ngy sinh </label>
                <datePicker
                  placeholder="DD/MM/YYYY"
                  :enterSubmit="true"
                  :tabSubmit="true"
                  :maxDate="new Date()"
                  :minDate="minDate"
                  locale="vn"
                  format="dd/MM/yyyy"
                  textInput
                  :dayNames="['T2', 'T3', 'T4', 'T5', 'T6', 'T7', 'CN']"
                  autoApply
                  utc
                  v-model="employee.dateOfBirth"
                />
                <!-- <input
                  tabindex="5"
                  class="input input_date"
                  type="date"
                  :max="maxDate"
                  v-model="employee.dateOfBirth"
                  :class="{ input__error: errors.errorDate }"
                  dateError="dateError"
                /> -->
                <div :class="{ input__show__error: errors.errorDate }">
                  {{ errors.errorDate }}
                </div>
              </div>
              <div class="container__input input__margin_16 input__320">
                <label class="input__label" for=""> Gii tnh </label>
                <div class="container__gender">
                  <input
                    type="radio"
                    name="gender"
                    id="male"
                    value="0"
                    checked
                    v-model="employee.gender"
                  />
                  <label for="male">Nam</label>
                  <input
                    type="radio"
                    name="gender"
                    id="female"
                    value="1"
                    v-model="employee.gender"
                  />
                  <label for="">N</label>
                  <input
                    type="radio"
                    name="gender"
                    id="other"
                    value="2"
                    v-model="employee.gender"
                  />
                  <label for="other">Khc</label>
                </div>
              </div>
            </div>
          </div>
          <div class="modal__body__row">
            <div class="body__row__left">
              <div class="container__input input__480">
                <label class="input__label" for=""
                  >n v <span class="required">*</span></label
                >

                <MCombobox
                  :url="urlDepartment"
                  propValue="departmentID"
                  propText="departmentName"
                  @getValueDepart="getValueDepart"
                  @blurValidate="blurValidate"
                  :departmentName="employee.departmentName"
                  :errorDepartment="errors.errorDepartment"
                  ref="empDepartment"
                ></MCombobox>
              </div>
            </div>
            <div class="body__row__rigth">
              <div class="container__input input__320">
                <label
                  class="input__label"
                  for=""
                  v-tooltip.left="{
                    content: 'S chng minh nhn dn',
                  }"
                  >S CMND</label
                >
                <input
                  class="input"
                  type="text"
                  maxlength="15"
                  placeholder="S chng minh nhn dn"
                  v-model="employee.identityNumber"
                />
              </div>
              <div class="container__input input__margin_6 input__160">
                <label class="input__label" for="">Ngy cp</label>
                <datePicker
                  placeholder="DD/MM/YYYY"
                  :enterSubmit="true"
                  :tabSubmit="true"
                  :maxDate="new Date()"
                  :minDate="minDate"
                  locale="vn"
                  format="dd/MM/yyyy"
                  textInput
                  :dayNames="['T2', 'T3', 'T4', 'T5', 'T6', 'T7', 'CN']"
                  autoApply
                  utc
                  v-model="employee.identityIssuedDate"
                />
                <!-- <input
                  tabindex="10"
                  class="input input_date"
                  :max="maxDate"
                  type="date"
                  v-model="employee.identityIssuedDate"
                /> -->
              </div>
            </div>
          </div>
          <div class="modal__body__row">
            <div class="body__row__left">
              <div class="container__input input__480">
                <label class="input__label" for="">Chc danh</label>
                <input
                  class="input"
                  type="text"
                  maxlength="50"
                  placeholder="Chc danh"
                  v-model="employee.positionName"
                />
              </div>
            </div>
            <div class="body__row__rigth">
              <div class="container__input input__480">
                <label class="input__label" for="">Ni cp</label>
                <input
                  class="input"
                  type="text"
                  maxlength="150"
                  placeholder="Ni cp chng minh nhn dn"
                  v-model="employee.identityIssuedPlace"
                />
              </div>
            </div>
          </div>
          <div class="modal__body__row margin__top_16">
            <div class="container__input">
              <label class="input__label" for="">a ch</label>
              <input
                class="input"
                type="text"
                maxlength="255"
                placeholder="a ch"
                v-model="employee.address"
              />
            </div>
          </div>
          <div class="modal__body__row">
            <div class="container__input input__240">
              <label
                class="input__label"
                for=""
                v-tooltip.left="{
                  content: 'S in thoi di ng',
                }"
                >T di ng
              </label>
              <input
                class="input"
                type="text"
                placeholder="S a thoi di ng"
                v-model="employee.phoneNumberMobile"
              />
            </div>
            <div class="container__input input__margin_6 input__240">
              <label
                class="input__label"
                for=""
                v-tooltip.left="{
                  content: 'S in thoi c nh',
                }"
                >T c nh
              </label>
              <input
                class="input"
                type="text"
                placeholder="S in thoi c nh"
                v-model="employee.phoneNumberLandline"
              />
            </div>
            <div class="container__input input__margin_6 input__240">
              <label class="input__label" for="">Email </label>
              <input
                class="input"
                type="text"
                maxlength="100"
                placeholder="Email"
                errorCode="errorEmail"
                ref="empEmail"
                v-model="employee.email"
                :class="{ input__error: errors.errorEmail }"
                @blur="blurValidate"
              />
              <div :class="{ input__show__error: errors.errorEmail }">
                {{ errors.errorEmail }}
              </div>
            </div>
          </div>
          <div class="modal__body__row">
            <div class="container__input input__240">
              <label class="input__label" for="">Ti khon ngn hng </label>
              <input
                class="input"
                type="text"
                maxlength="20"
                placeholder="Ti khon ngn hng"
                v-model="employee.accountBank"
              />
            </div>
            <div class="container__input input__margin_6 input__240">
              <label class="input__label" for="">Tn ngn hng </label>
              <input
                class="input"
                type="text"
                maxlength="50"
                placeholder="Tn ngn hng"
                v-model="employee.nameBank"
              />
            </div>
            <div class="container__input input__margin_6 input__240">
              <label class="input__label" for=""> Chi nhnh </label>
              <input
                class="input"
                type="text"
                maxlength="50"
                placeholder="Chi nhnh ngn hng"
                v-model="employee.branchBank"
              />
            </div>
          </div>
        </div>
        <div class="modal__footer">
          <div class="btn__base content__center" @click="closeModal">Hy</div>
          <div class="modal__footer__rigth">
            <div
              class="btn__base content__center"
              v-tooltip="{
                content: 'Ct (Ctrl + S)',
              }"
              @click="saveModal"
              @keypress.enter="saveModal"
            >
              Ct
            </div>
            <div
              class="btn margin__letf_8"
              v-tooltip="{
                content: 'Ct (Ctrl + Shift + S)',
              }"
              @click="saveModalAdd"
              @keypress.enter="saveModalAdd"
            >
              Ct v thm
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <DialogFormClose
    v-if="isCloseForm"
    @closeModal="closeModal"
    @hideCloseForm="hideCloseForm"
    @saveModal="saveModal"
  ></DialogFormClose>
  <DialogError
    v-if="isErrorFrom"
    @hideErrorFrom="hideErrorFrom"
    :textPopupError="textPopupError"
    :isFooterFE="isFooterFE"
    :isFooterBE="isFooterBE"
  ></DialogError>
</template>
<script>
import Common from "../../script/common/common";
import Enum from "../../script/common/enumeration";
import DialogFormClose from "../../components/base/MDialogFormClose.vue";
import MCombobox from "../../components/base/msCombobox/MCombobox.vue";
import DialogError from "../../components/base/MDialog.vue";
const urlBase = process.env.VUE_APP_URL;
export default {
  name: "EmployeeDetailComponent",
  components: {
    DialogFormClose,
    MCombobox,
    DialogError,
  },
  props: {
    employeeSelect: Object,
    formMode: Number,
  },
  // Khai bo cc emit t componet cha
  emits: [
    "notLoadingData",
    "loadingData",
    "hideModal",
    "showModal",
    "resetModal",
    "handleToast",
  ],
  created() {
    this.employee = this.employeeSelect;
     //Thm m nhn vin mi khi mi from thm mi
    if (this.formMode === Enum.FormMode.Add) {
      this.newEmployeeCode();
    }
  },
  mounted() {
    /*
     *Dng  focus vo  u tin
     */
    this.$refs.empCode.focus();
  
    /*
     *Dng  format ngy thng
     */
    this.formatDateEmployee();
  },
  beforeUpdate() {
    this.changeData = true;
  },
  data() {
    return {
      employee: {},
      isCloseForm: false,
      errors: {
        errorCode: "",
        errorName: "",
        errorDepartment: "",
        errorEmail: "",
        errorDate: "",
      },
      urlDepartment: `${urlBase}/Departments`,
      saveMode: Enum.SaveMode.Save,
      isErrorFrom: false,
      textErrorPopup: "",
      refName: " ",
      isFooterFE: false,
      isFooterBE: false,
      minDate: new Date("1900-01-01"),
    };
  },
  methods: {
    ///
    /// Cc hm  dng  format
    ///
    /*
     * Hm dng  format form khi lu vo
     * PCTUANANH(16/09/2022)
     */
    formatInputForm() {
      this.traneGenderNumber();
    },
    /*
     * Hm dng  format ngy thng trong employee
     * PCTUANANH(16/09/2022)
     */
    formatDateEmployee() {
      if (this.formMode == Enum.FormMode.Edit) {
        this.employee.dateOfBirth = Common.formatDate2(
          this.employee.dateOfBirth
        );
        this.employee.identityIssuedDate = Common.formatDate2(
          this.employee.identityIssuedDate
        );
      }
    },

    /*
     * Hm dng  format gii tnh t chui sang s
     * PCTUANANH(16/09/2022)
     */
    traneGenderNumber() {
      if (!this.employee.gender) {
        this.employee.gender = 0;
      } else {
        this.employee.gender = Number(this.employee.gender);
      }
    },
    /*
     * Hm dng   thm mi mt m nhn vin t ng tng
     * PCTUANANH(13/09/2022)
     */
    newEmployeeCode() {
      try {
        fetch(`${urlBase}/Employees/new-code`)
          .then((res) => res.text())
          .then((res) => {
            this.employee.employeeCode = res;
          })
          .catch((error) => {
            console.log("Error! Could not reach the API. " + error);
            this.showErrorServer();
          });
      } catch (error) {
        console.log(error);
      }
    },
    ///
    /// Cc hm  x  l ng
    ///
    /*
     * Hm dng  ng modal bng nt Hy
     * PCTUANANH(18/09/2022)
     */
    closeModal() {
      this.$emit("notLoadingData");
      this.$emit("hideModal");
    },
    /*
     * Hm dng  ng modal bng nt X
     * PCTUANANH(18/09/2022)
     */
    isCloseModal() {
      if (this.formMode === Enum.FormMode.Add) {
        if (this.employee != {}) {
          this.isCloseForm = true;
        } else {
          this.isCloseForm = false;
          this.closeModal();
        }
      } else {
        this.isCloseForm = true;
      }
    },
    /*
     * Hm dng  n form close bng nt hy
     * PCTUANANH(18/09/2022)
     */
    hideCloseForm() {
      this.isCloseForm = false;
    },
    /*
     * Hm dng  n popup show Error
     * PCTUANANH(28/09/2022)
     */
    hideErrorFrom() {
      this.isErrorFrom = false;
      if (this.isFooterFE) {
        if (this.refName) {
          if (this.refName === "empDepartment") {
            this.$refs.empDepartment.departmentForcus();
          } else {
            this.$refs[this.refName].focus();
          }
        }
      } else {
        this.$refs.empCode.focus();
      }
    },
    /*
     * Hm dng  validate
     * PCTUANANH(24/09/2022)
     */
    validate() {
      let isValidate = true;
      this.errors = {
        errorCode: "",
        errorName: "",
        errorDepartment: "",
        errorEmail: "",
      };
      if (!this.employee.employeeCode) {
        this.errors.errorCode = Enum.Errors.errorCode;
        isValidate = false;
      }
      if (!this.employee.employeeName) {
        this.errors.errorName = Enum.Errors.errorName;
        isValidate = false;
      }
      if (!this.employee.departmentID) {
        this.errors.errorDepartment = Enum.Errors.errorDepartment;
        isValidate = false;
      }
      if (this.employee.email) {
        if (!/^[\w-.]+@([\w-]+.)+[\w-]{2,4}$/.test(this.employee.email)) {
          this.errors.errorEmail = Enum.Errors.errorEmail;
          isValidate = false;
        }
      }

      this.validateSave();
      return isValidate;
    },
    /*
     * Hm dng   validate khi ct
     * PCTUANANH(10/10/2022)
     */
    validateSave() {
      if (!this.employee.employeeCode) {
        this.isErrorFrom = true;
        this.textPopupError = Enum.Errors.errorCode;
        this.refName = "empCode";
        this.isFooterFE = true;
        return;
      }
      if (!this.employee.employeeName) {
        this.isErrorFrom = true;
        this.textPopupError = Enum.Errors.errorName;
        this.refName = "empName";
        this.isFooterFE = true;
        return;
      }
      if (!this.employee.departmentID) {
        this.isErrorFrom = true;
        this.textPopupError = Enum.Errors.errorDepartment;
        this.refName = "empDepartment";
        this.isFooterFE = true;
        return;
      }
      if (this.employee.email) {
        if (!/^[\w-.]+@([\w-]+.)+[\w-]{2,4}$/.test(this.employee.email)) {
          this.isErrorFrom = true;
          this.textPopupError = Enum.Errors.errorEmail;
          this.refName = "empEmail";
          this.isFooterFE = true;
          return;
        }
      }
    },
    /*
     * Hm dng  blur validate
     * PCTUANANH(27/09/2022)
     */
    blurValidate(e) {
      let errorCode = e.currentTarget.getAttribute("errorCode");
      if (errorCode === "errorEmail") {
        if (e.currentTarget.value) {
          let valueEmial = e.currentTarget.value;
          if (!/^[\w-.]+@([\w-]+.)+[\w-]{2,4}$/.test(valueEmial)) {
            this.errors[errorCode] = Enum.Errors[errorCode];
          } else {
            this.errors[errorCode] = "";
          }
        }
      } else if (e.currentTarget.value) {
        this.errors[errorCode] = "";
      } else {
        this.errors[errorCode] = Enum.Errors[errorCode];
      }
    },
    /*
     * Hm dng  validate backend
     * PCTUANANH(28/09/2022)
     */
    validateBackend(response, employeeCode) {
      if (
        response.errorCode &&
        response.errorCode === Enum.resErrorCodes.errorDuplicate
      ) {
        this.isErrorFrom = true;
        this.textPopupError = `${Enum.textErrorBackend.textCodeLeft} <${employeeCode}> ${Enum.textErrorBackend.textCodeRight}`;
      } else {
        {
          if (this.saveMode === Enum.SaveMode.Save) {
            this.$emit("loadingData");
            this.$emit("hideModal");
          }
          // Nu l Ct v thm th s reset li from, v m nhn vin tng thm
          if (this.saveMode === Enum.SaveMode.SaveAdd) {
            this.$emit("loadingData");
            setTimeout(() => {
              this.$emit("notLoadingData");
            }, 500);
            this.$emit("resetModal");
            this.employee = {};
            this.newEmployeeCode();
            this.$refs.empCode.focus();
            this.$refs.empDepartment.textInput = "";
            this.$refs.empDepartment.indexItemSelected = null;
          }
        }
      }
    },
    /*
     * Hm dng  hin li khi c li t server
     * PCTUANANH(10/10/2022)
     */
    showErrorServer() {
      this.isErrorFrom = true;
      this.textPopupError = Enum.textErrorBackend.textServer;
    },
    /*
     * Hm dng  gi tr ca Department
     * PCTUANANH(24/09/2022)
     */
    getValueDepart(department) {
      this.employee.departmentID = department.departmentID;
      this.employee.departmentName = department.departmentName;
      if (this.employee.departmentID) {
        this.errors.errorDepartment = "";
      }
    },
    ///
    /// Cc hm dng  lu
    ///
    handleSave() {
      try {
        if (!this.validate()) {
          this.isCloseForm = false;
        }
        if (this.validate()) {
          // sa nhn vin
          if (this.formMode === Enum.FormMode.Edit) {
            this.saveEditEmployee();
            this.$emit("handleToast", Enum.textToast.updateSuccess);
          }
          // thm mi nhn vin
          else if (this.formMode === Enum.FormMode.Add) {
            this.saveAddEmployee();
            this.$emit("handleToast", Enum.textToast.addSuccess);
          }
        }
      } catch (error) {
        console.log(error);
      }
    },
    /*
     * Hm dng  lu  modal
     * PCTUANANH(12/09/2022)
     */
    saveModal() {
      this.saveMode = Enum.SaveMode.Save;
      this.handleSave();
    },
    /*
     * Hm dng  lu  modal v thm mi modal
     * PCTUANANH(12/09/2022)
     */
    saveModalAdd() {
      this.saveMode = Enum.SaveMode.SaveAdd;
      this.handleSave();
    },
    /*
     * Hm dng  lu  modal v thm mi modal
     * PCTUANANH(03/10/2022)
     */
    handleKeyModel(e) {
      if (e.keyCode === Enum.keyCode.ESC) {
        e.preventDefault();
        this.isCloseModal();
      }
      if (e.keyCode === Enum.keyCode.S && e.ctrlKey) {
          e.preventDefault();
          this.saveModal();
        }
      if (e.keyCode === (Enum.keyCode.S && e.ctrlKey && e.shiftKey)) {
          e.preventDefault();
          this.saveModalAdd();
        }
      }
    ,
    ///
    /// Cc hm    thm, sa  gi n API
    ///
    /*
     * Hm dng  gi api  sa nhn vin
     * PCTUANANH(16/09/2022)
     */
    saveEditEmployee() {
      try {
        this.formatInputForm();
        let data = this.employee;
        let url = `${urlBase}/Employees/${this.employee.employeeID}`;
        fetch(url, {
          method: "PUT",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(data),
        })
          .then((response) => response.json())
          .then((response) => {
            this.validateBackend(response, data.employeeCode);
          })
          .catch((error) => {
            console.error("Error:", error);
            this.showErrorServer();
          });
      } catch (error) {
        console.log(error);
      }
    },
    /*
     * Hm dng  gi api  thm mi nhn vin
     * PCTUANANH(16/09/2022)
     */
    saveAddEmployee() {
      try {
        this.formatInputForm();
        let data = this.employee;
        let url = `${urlBase}/Employees`;
        fetch(url, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(data),
        })
          .then((response) => response.json())
          .then((response) => {
            this.validateBackend(response, data.employeeCode);
          })
          .catch((error) => {
            console.error("Error:", error);
            this.showErrorServer();
          });
      } catch (error) {
        console.log(error);
      }
    },
  },
};
</script>